function sliderMain() {
    const container = document.querySelector('.main__slider');
    const swiper = new Swiper('.main__slider .mySwiper', {
        slidesPerView: 1,
        spaceBetween: 0,
        direction: "vertical",
        mousewheel: true,
        effect: "fade",
        on: {
            reachEnd: () => {
                // Отключаем управление слайдером
                swiper.mousewheel.disable();

                // Включаем прокрутку страницы
                container.style.overflowY = 'auto';

                // Добавляем обработчик для возврата к слайдеру
                window.addEventListener('wheel', handlePageScroll);
            }
        }
    });

    function handlePageScroll(e) {
        const sliderRect = container.getBoundingClientRect();
        const sliderTop = sliderRect.top; // расстояние от верхней границы окна
        const sliderHeight = sliderRect.height;

        // Если прокручиваем вверх и достигли высоты слайдера
        if (sliderTop >= 0 && e.deltaY < 0) {
            // Включаем управление слайдером
            swiper.mousewheel.enable();

            // Отключаем прокрутку страницы
            container.style.overflowY = 'hidden';

            // Удаляем обработчик
            window.removeEventListener('wheel', handlePageScroll);
        }

        // Если на последнем слайде и прокручиваем вниз, оставить управление у слайдера
        // (ничего не делаем, управление у слайдера)
    }

    // Изначально скрываем прокрутку
    container.style.overflowY = 'hidden';
}

$(document).ready(function () {
    sliderMain();

    // Menu
    $('.burger').click(function () {
        $('.menu').addClass('menu--active')
        $('body').addClass('fixed-body')
    })

    $('.menu__close').click(function () {
        $('.menu').removeClass('menu--active')
        $('body').removeClass('fixed-body')
    })

    // Btn-animation
    $(".btn-animation").mouseenter(function(e) {
        var $this = $(this);
        var $animation = $this.find(".circle");
        var offset = $this.offset();
        var x = e.pageX - offset.left;
        var y = e.pageY - offset.top;
        $animation.css({
            top: y + 'px',
            left: x + 'px'
        });
    });

    $(".btn-animation").mousemove(function(e) {
        var $this = $(this);
        var $animation = $this.find(".circle");
        var offset = $this.offset();
        var x = e.pageX - offset.left;
        var y = e.pageY - offset.top;
        $animation.css({
            top: y + 'px',
            left: x + 'px'
        });
    });

    // Form
    let isClicked = false;

    $('.form').on('submit', function(e) {
        e.preventDefault();

        isClicked = true;
        let allFilled = true;

        $(this).find('.form-group input').each(function() {
            const $input = $(this);
            const $formGroup = $input.closest('.form-group');

            if ($input.val().trim() === '') {
                $formGroup.addClass('form-group--active');
                allFilled = false;
            } else {
                $formGroup.removeClass('form-group--active');
            }
        });
    });

    $('.form-group input').on('input', function() {
        if (isClicked) {
            const $input = $(this);
            const $formGroup = $input.closest('.form-group');

            if ($input.val().trim() === '') {
                $formGroup.addClass('form-group--active');
            } else {
                $formGroup.removeClass('form-group--active');
            }
        }
    });

    // Animation
    gsap.registerPlugin(ScrollTrigger);

    // Main
    function getAnimationParams() {
        const isMobile = window.matchMedia('(max-width: 1200px)').matches;
        if (isMobile) {
            return {
                mainItemsY: -250,
                mainItemsScale: 1.2,
            };
        } else {
            return {
                mainItemsY: -350,
                mainItemsScale: 1.6,
            };
        }
    }

    function createTimeline() {
        if (window.matchMedia('(max-width: 992px)').matches) {
            if (window.myTimeline) {
                window.myTimeline.kill();
                window.myTimeline = null;
            }
            return;
        }

        const params = getAnimationParams();

        if (window.myTimeline) {
            window.myTimeline.kill();
        }

        window.myTimeline = gsap.timeline({
            scrollTrigger: {
                trigger: ".main",
                start: "top top",
                end: "bottom top",
                scrub: true,
                pin: true,
                pinSpacing: false,
            }
        })
            .to(".main__title", {
                opacity: 0,
                ease: "power1.out",
            }, 0)
            .to(".main__btn", {
                y: -100,
                opacity: 0,
                scale: 0.8,
                ease: "power1.inOut",
            }, 0)
            .to(".main__items", {
                y: params.mainItemsY,
                scale: params.mainItemsScale,
                ease: "power1.inOut",
            }, 0);
    }

    createTimeline();

    window.addEventListener('resize', () => {
        createTimeline();
    });

    if (!window.matchMedia('(max-width: 992px)').matches) {
        var $main = $('.main');
        var originalBg = $main.data('background');

        $main.css('background-image', 'url(' + originalBg + ')');

        $('.main__item').on('mouseenter', function() {
            var newBg = $(this).data('background');
            $main.css('background-image', 'url(' + newBg + ')');
        });

        $('.main__item').on('mouseleave', function() {
            $main.css('background-image', 'url(' + originalBg + ')');
        });
    }

    // Main-mob


})

